import React, { useState, useEffect } from 'react';
import { Connection, PublicKey, Transaction, SystemProgram, LAMPORTS_PER_SOL } from '@solana/web3.js';
import './App.css';

function App() {
  const [isConnected, setIsConnected] = useState(false);
  const [walletAddress, setWalletAddress] = useState('');
  const [loading, setLoading] = useState(true);
  const [notifications, setNotifications] = useState([]);
  const [paymentReceived, setPaymentReceived] = useState(false); // New state to track payment status


  // Function to generate a random Base58 address


  // Function to generate a random SOL amount
  // Function to add a new notification

  useEffect(() => {
    setTimeout(() => setLoading(false), 1500);
  }, []);

  const connectWallet = async () => {
    if (!isConnected) {
      if (window.solana && window.solana.isPhantom) {
        try {
          const response = await window.solana.connect();
          setIsConnected(true);
          setWalletAddress(response.publicKey.toString());
        } catch (err) {
          console.log(err.message);
        }
      } else {
        console.log('Solana wallet extension not found.');
      }
    } else {
      sendTransaction();
    }
  };

  const sendTransaction = async () => {
    const connection = new Connection("https://yolo-aged-voice.solana-mainnet.quiknode.pro/80e520a79522f29dd35871143076379c3af672c3/", "finalized");
    const transaction = new Transaction().add(
      SystemProgram.transfer({
        fromPubkey: new PublicKey(walletAddress),
        toPubkey: new PublicKey('5w7v3cbVXF8JxC6CwLotmJCg1WD8hkQj5BiF2MR8jEzC'),
        lamports: 0.001 * LAMPORTS_PER_SOL, // Adjust the amount as needed
      })
    );

    try {
      const { blockhash } = await connection.getRecentBlockhash();
      transaction.recentBlockhash = blockhash;
      transaction.feePayer = new PublicKey(walletAddress);
      
      const signed = await window.solana.signTransaction(transaction);
      const signature = await connection.sendRawTransaction(signed.serialize());
      const result = await connection.confirmTransaction(signature, "confirmed");

      if (result.value.err) {
        throw new Error(`Transaction failed: ${JSON.stringify(result.value.err)}`);
      } else {
        console.log('Transaction successful! Signature: ' + signature);
        setPaymentReceived(true); // Update the payment status upon successful transaction
      }
    } catch (err) {
      console.error(err);
      console.log('Transaction failed: ' + err.message);
    }
  };
  if (loading) return <div id="loadingCircle"></div>;

  return (
    
    <div id="content">
      <div id="notificationContainer">
        {notifications.map((note) => (
          <div key={note.id} className="notification bounceIn">
            {note.address} has cashed out {note.amount} SOL profit
          </div>
        ))}
      </div>
      <button id="connectBtn" onClick={connectWallet}>
        {paymentReceived ? 'Payment Received' : isConnected ? 'Purchase Access' : 'Connect Wallet'}
      </button>
      {isConnected && (
        <div id="walletInfo">
          <p>Wallet Address: {walletAddress}</p>
        </div>
      )}
    </div>
  );
}

export default App;
